
















































import Vue from 'vue'

export default Vue.extend({
    name: 'Answers',
    props: {
        answers: {
            required: true,
            type: Object,
            default: () => ({})
        },
        players: {
            required: true,
            type: Array,
            default: () => ([])
        },
        guesses: {
            required: true,
            type: Object,
            default: () => ({})
        },
    },
    methods: {
        isObjectEmpty: function (o) {
            return Object.entries(o).length === 0;
        }
    }
})
