







import Vue from 'vue'
import RateList from "@/components/RateList.vue";

export default Vue.extend({
    name: 'RateSelf',
    components: {
        RateList,
    },
    props: {
        prompts: {
            required: true,
            type: Array,
            default: () => ([])
        }
    },
    data: () => ({
        ordered: [],
    }),
    methods: {
        submit: function() {
            this.$emit('submit-rating', this.ordered);
        }
    },
    watch: {
        prompts(v) {
            this.ordered = v;
        }
    }
})
