
















import Vue from 'vue'
import RoomInfo from './RoomInfo.vue'
import PromptsWrite from './PromptsWrite.vue'
import RateSelf from './RateSelf.vue'
import Guess from './Guess.vue'
import Answers from './Answers.vue'


export default Vue.extend({
    name: 'Game',
    components: {
        RoomInfo,
        PromptsWrite,
        RateSelf,
        Guess,
        Answers,
    },
    props: {
        gameId: {
            required: true,
            type: String,
            default: '',
        },
        players: {
            required: true,
            type: Array,
            default: () => ([]),
        },
        leader: {
            required: true,
            type: String,
            default: '',
        },
        name: {
            required: true,
            type: String,
            default: '',
        },
        prompts: {
            required: true,
            type: Array,
            default: () => ([]),
        },
        state: {
            required: true,
            type: String,
            default: '',
        },
        guesses: {
            required: true,
            type: Object,
            default: () => ({}),
        },
        allGuesses: {
            required: true,
            type: Object,
            default: () => ({}),
        },
        answers: {
            required: true,
            type: Object,
            default: () => ({}),
        },
    },
    methods: {
        start: function () {
            this.$emit('start-game');
        },
        submitPrompts: function (e) {
            this.$emit('submit-prompts', e);
        },
        submitRating: function (e) {
            this.$emit('submit-rating', e);
        },
        submitGuesses: function (e) {
            this.$emit('submit-guesses', e);
        }
    }
})
