



































import Vue from 'vue'

export default Vue.extend({
    name: 'Connect',

    data: () => ({
        name: '',
        gameId: ''
    }),

    methods: {
        join: function () {
            this.$emit('join-game', this.name, this.gameId);
        },
        create: function () {
            this.$emit('create-game', this.name);
        }
    }
})
