










import Vue from 'vue'
import draggable from 'vuedraggable';

export default Vue.extend({
    name: 'RateList',
    components: {
        draggable,
    },
    props: {
        value: {
            required: false,
            type: Array,
            default: () => ([])
        }
    },
    data: () => ({
        list: [],
    }),
    watch: {
        value: {
            handler(v) {
                this.list = v
            },
            immediate: true
        }
    }
})
