






























import Vue from 'vue'

export default Vue.extend({
    name: 'RoomInfo',
    props: {
        gameId: {
            required: true,
            type: String,
            default: '',
        },
        players: {
            required: true,
            type: Array,
            default: () => [],
        },
        leader: {
            required: true,
            type: String,
            default: '',
        },
        name: {
            required: true,
            type: String,
            default: '',
        },
        state: {
            required: true,
            type: String,
            default: '',
        },
    },
    methods: {
        start: function () {
            this.$emit('start-game');
        }
    }
})
