


















import Vue from 'vue'

export default Vue.extend({
    name: 'Game',
    data: () => ({
        prompt1: '',
        prompt2: '',
        prompt3: ''
    }),
    methods: {
        submit: function () {
            this.$emit('submit-prompts', [this.prompt1, this.prompt2, this.prompt3]);
        }
    }
})
